.bar {
	position: relative;
	&__bg {
		position: absolute;
		&:nth-child(1) {
			right: 0;
			top: 50%;
		}
	}
}

.bar-block {
	width: 100%;
	position: relative;
	padding: 270px 0 0 0;
	&__bg {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 600px;
		left: 0px;
		top: 0px;
		background: #fefbec;
		z-index: -1;
	}
	&__containers {
		max-width: 1440px;
		width: 100%;
		margin: 0 auto;
		padding: 0 60px;
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		justify-content: flex-start;
	}
	&__title {
		margin: 0 0 65px 0;
		& p {
			font-family: Prata;
			font-style: normal;
			font-weight: normal;
			font-size: 54px;
			line-height: 50px;
			color: #021245;
		}
		&-filter {
			width: 53px;
			height: 53px;
			background: #ffffff;
			border: 2px solid #003f80;
			display: flex;
			align-items: center;
			justify-content: center;
			cursor: pointer;
			border-radius: 50%;
			display: none;
		}
	}
